@import url("https://fonts.googleapis.com/css?family=Goudy+Bookletter+1911");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Goudy Bookletter\ 1911", sans-serif; }

html {
  background-color: #69bcd3;
  height: 100%; }

.App {
  max-width: 1024px;
  margin: 0 auto;
  text-align: center; }

.title {
  margin: 20px 0; }

.controls .input-field {
  padding: 10px 5px;
  border-radius: 3px 0 0 3px;
  border: 1px solid transparent;
  border-color: #d6d6d6;
  width: 200px;
  font-size: 16px;
  box-shadow: inset 0px 0px 3px 0px #bdbdbd; }

.controls .search-button {
  padding: 11px;
  border-radius: 0 3px 3px 0;
  border: none;
  background-color: #1fd847;
  color: #fff;
  width: 75px;
  font-size: 16px; }

.checkbox-container {
  margin: 20px auto; }
  .checkbox-container input.checkboxes {
    margin: 0 5px 0 0; }
  .checkbox-container .checkbox {
    margin: 0 15px 0 0;
    font-size: 14px; }

section {
  margin: 20px 30px;
  /*  Media queries */ }
  section .recipe {
    background-color: #fff;
    border-radius: 5px;
    padding: 10px;
    margin: 20px auto;
    max-width: 1000px; }
    section .recipe .img-container {
      margin: 0 auto 10px auto;
      max-width: 170px; }
      section .recipe .img-container a {
        color: #f37c0c;
        text-decoration: none; }
      section .recipe .img-container a:hover {
        color: #b4671e; }
      section .recipe .img-container .recipe-img {
        width: 100%; }
    section .recipe .recipe-content {
      display: flex; }
      section .recipe .recipe-content .recipe-div-1 {
        width: 33.3333%;
        padding: 10px; }
        section .recipe .recipe-content .recipe-div-1 .recipe-info {
          margin: 0 0 15px 0; }
        section .recipe .recipe-content .recipe-div-1 p,
        section .recipe .recipe-content .recipe-div-1 li {
          text-align: left;
          list-style: none;
          font-size: 18px; }
      section .recipe .recipe-content .recipe-div-2 {
        padding: 10px;
        width: 66.6666%; }
        section .recipe .recipe-content .recipe-div-2 li {
          text-align: left;
          margin: 0 0 0 20px;
          font-size: 18px; }
  @media screen and (max-width: 764px) {
    section .recipe .recipe-content {
      display: block; }
      section .recipe .recipe-content .recipe-div-1,
      section .recipe .recipe-content .recipe-div-2 {
        width: 100%; } }
